import React from 'react';
import { ImShuffle } from 'react-icons/im';
import { QuoteConsumer } from '../contexts/quoteContext';

function Header() {
  return (
    <QuoteConsumer>
      {({ isLoading, quoteObj, generateQuote }) => (
        <>
          <h1 className="heading-primary">Quote of the Day</h1>
          {!isLoading && (
            <h3 className="heading-tertiary">
              Find a quote that resonates with you and share it with the world!
            </h3>
          )}
          {!isLoading && !quoteObj && (
            <>
              <button className="btn-cta" onClick={generateQuote}>
                <ImShuffle className="icon-svg" />
              </button>
              <p className="instructional-text">
                Click the <span className="user-instruction">"SHUFFLE"</span>
                &nbsp;button to get your quote!
              </p>
              <p className="instructional-text desktop">
                ( or press the <span className="user-instruction">"ENTER"</span>
                &nbsp;or <span className="user-instruction">"SPACEBAR"</span>
                &nbsp;keys )
              </p>
            </>
          )}
        </>
      )}
    </QuoteConsumer>
  );
}

export default Header;
