import React, { Component } from 'react';
import { QuoteProvider } from './contexts/quoteContext';
import './App.css';
import Header from './components/Header';
import QuoteCard from './components/QuoteCard';
import FetchError from './components/FetchError';
import Footer from './components/Footer';
import Loader from './components/Loader';
import colors from './data/colors';

class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      isLoading: false,
      quotes: [],
      errMsg: '',
      quoteObj: '',
      quoteChanged: false,
      randomColor: 'black',
    };

    this.getQuotes = this.getQuotes.bind(this);
  }

  componentDidMount() {
    this.getQuotes();
  }

  getQuotes() {
    this.setState({ isLoading: true });
    setTimeout(() => {
      fetch(
        'https://gist.githubusercontent.com/camperbot/5a022b72e96c4c9585c32bf6a75f62d9/raw/e3c6895ce42069f0ee7e991229064f167fe8ccdc/quotes.json'
      )
        .then((res) => res.json())
        .then((data) =>
          this.setState(
            { isLoading: false, quotes: data.quotes },
            () =>
              (document.body.onkeyup = (e) =>
                ['Space', 'Enter'].includes(e.code) && this.generateQuote())
          )
        )
        .catch((err) =>
          this.setState({ isLoading: false, errMsg: err.message })
        );
    }, 2500);
  }

  generateQuote = (e) => {
    this.setState({ quoteChanged: true });
    let quoteIdx = Math.ceil(Math.random() * 101);
    setTimeout(
      () => {
        this.setState({
          quoteObj: this.state.quotes[quoteIdx],
          quoteChanged: false,
          randomColor: colors[Math.ceil(Math.random() * 11)],
        });
      },
      !this.state.quoteObj ? 0 : 1100
    );
  };

  render() {
    let {
      state: { randomColor, isLoading, quoteObj, errMsg },
      generateQuote,
    } = this;
    return (
      <div className="app" style={{ background: randomColor }}>
        <QuoteProvider value={{ ...this.state, generateQuote }}>
          {isLoading ? <Loader /> : errMsg ? <FetchError /> : <Header />}
          {quoteObj && (
            <>
              <QuoteCard />
              <Footer />
            </>
          )}
        </QuoteProvider>
      </div>
    );
  }
}

export default App;
