import React from 'react';
import { FaQuoteLeft } from 'react-icons/fa';
import { QuoteConsumer } from '../contexts/quoteContext';

function QuoteContainer() {
  return (
    <QuoteConsumer>
      {({ randomColor: color, quoteObj: { quote, author }, quoteChanged }) => (
        <div
          className={quoteChanged ? 'quote-container fade' : 'quote-container'}
          style={{ color }}
        >
          <p className="quote-text">
            <FaQuoteLeft className="icon-svg" />
            &nbsp;{quote}
          </p>

          <div className="quote-author">
            <span>- {author}</span>
          </div>
        </div>
      )}
    </QuoteConsumer>
  );
}

export default QuoteContainer;
