import React from 'react';

const Loader = () => {
  return (
    <>
      <div className="loader">
        <div className="loading-circle"></div>
        <div className="loading-circle"></div>
        <div className="loading-circle"></div>
      </div>
      <p className="loading-text">Loading. Please wait...</p>
    </>
  );
};

export default Loader;
