import React from 'react';
import { QuoteConsumer } from '../contexts/quoteContext';
import { FaTwitter, FaFacebook, FaWhatsapp } from 'react-icons/fa';
import { ImShuffle } from 'react-icons/im';

function QuoteButtons() {
  return (
    <QuoteConsumer>
      {({ randomColor, quoteObj: { quote, author }, generateQuote }) => (
        <div className="buttons-container">
          <div className="sm-buttons-container">
            <button
              className="btn-socialMedia"
              style={{ background: randomColor }}
            >
              <a
                className="link-socialMedia"
                title="Tweet this quote!"
                href={`https://twitter.com/intent/tweet?text=${quote} - ${author} %23QuoteOfTheDay`}
                target="_blank"
                rel="noopener noreferrer"
              >
                <FaTwitter className="icon-svg" size="1.3em" />
              </a>
            </button>
            <button
              className="btn-socialMedia"
              style={{ background: randomColor }}
            >
              <a
                className="link-socialMedia"
                title="Share this quote on WhatsApp!"
                href={`whatsapp://send?text=${quote} - ${author} %23QuoteOfTheDay`}
                target="_blank"
                rel="noopener noreferrer"
              >
                <FaWhatsapp className="icon-svg" size="1.3em" />{' '}
              </a>
            </button>
            <button
              className="btn-socialMedia"
              style={{ background: randomColor }}
            >
              <a
                className="link-socialMedia"
                title="Share this page on Facebook!"
                href="https://www.facebook.com/sharer/sharer.php?u=https://xandert93.github.io/drum-machine/"
                target="_blank"
                rel="noopener noreferrer"
              >
                <FaFacebook className="icon-svg" size="1.3em" />
                {/*FB T&C's state that you cannot share any text until the user types it in FB fields*/}
              </a>
            </button>
          </div>

          <div className="quote-button-container">
            <button
              className="btn-shuffle"
              title="Shuffle"
              onClick={(e) => {
                e.currentTarget.blur();
                console.log(e.currentTarget);
                generateQuote();
              }}
              style={{ background: randomColor }}
            >
              <ImShuffle className="icon-svg" size="1.3em" />
            </button>
          </div>
        </div>
      )}
    </QuoteConsumer>
  );
}

export default QuoteButtons;
